.sidebar-section{
    display: flex;
}
.sidebar{
    height: 100vh;
    width: 70px;
    background-color: #1e1e2c;
    transition: width 0.2s ease;
    position: fixed;
}
.sidebar-toggle{
    width: 200px;
}
.sidebar-toggle-icon{
    display: flex;
    align-items: right;
    justify-content: right;
    cursor: pointer;
}
.sidebar-toggle-icon p svg{
    background-color: tomato;
    color: white;
    margin-top: 30px;
   
}

@media (max-width: 600px){
    .sidebar{
        display: none;
    }
}