/* About section */

.sidebar-width{
    margin-left: 200px;
    padding: 30px 20px;
}

.about-img{
    text-align: center;
    
    
}
.about-img img{
    width: 350px;
    border-radius: 10px;
 }
.about-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.about-content h1{
    font-family: cursive;
    color: silver;
    border-bottom: 2px solid silver;
}
.about-content p{
text-align: justify;
}


/* ==========Mobile Responsive================== */

@media (max-width:600px){
    .sidebar-width{
        margin: 0;
        padding: 0px 20px;
        margin-top: 20px;

    }
}
