.sidebar-width{
    margin-left: 200px;
    padding: 30px 20px;
}
.heading {
    color: tomato;
    text-transform: uppercase;
    display: inline-block;
    border-bottom: 3px solid tomato;
    margin-bottom: 30px;
}
.vertical-timeline-element-title{
    color: tomato;
}
.vertical-timeline-element-subtitle{
    color: grey;
}
.bg{
    background-color: rgba(128, 128, 128, 0.151);
    margin-top: 10px;
}

@media (max-width:600px){
    .sidebar-width{
        margin: 0;
        padding: 0px 20px;
        margin-top: 20px;

    }
   
}