
.profile-img img{
    border-radius: 50%;
    display: flex;
    height: 150px;
    width: 150px;
    vertical-align: middle;
    margin: 0 auto;
    color: white;
    margin-top: 15px;
    margin-bottom: 40px;
}
.nav-link{
    cursor: pointer;
}
.nav-link svg{
    margin-right: 10px;
    margin-top: -5px;
    cursor: pointer;
}
.nav-link .active{
    border-left: 5px solid tomato;
    padding-left: 10px;
    text-decoration: none;
    color: white;
} 
.nav-list{
    margin-left: 25px;
    margin-top: 40px;
}
.nav-list-icon{
    margin-top: 25px;
    text-align: center;
}

#dark{
    background-color: gray
}