.home-container{
    background-color: black;
    height: 270px;
    margin-left: -12px;
    margin-right: -12px;
    color: white;
    
}

.home-content {
    margin-left: 85px;
    padding-top: 90px;
    transition: 0.3s ease,
  }
  
  .home-content-toggle {
    margin-left: 165px;
    padding-left: 50px;
   
  }
  
.home-content h1{
    color: yellow;
}
.home-content-false h1{
    color: yellow;
}
.hire-me{
    background-color: green;
    width: 150px;
    color: white;
    margin: 0px 5px;
}
.btn-cv{
    background-color: tomato;
    width: 150px;
    color: white;
    margin: 0px 5px;
}
.btn-cv:hover{
    background-color: tomato;
    opacity: 80%;
    transition: 1s;
}
.hire-me:hover{
    background-color: green;
    width: 200px;
    transition: 1s ease;
}
.home-btn{
    background-color: #1e1e2c;
    width: 50px;
    height: 50px;
   display: flex;
   align-items: center;
   justify-content: center;
   position: fixed;
   cursor: pointer;
   border-top-left-radius: 10px;
   border-bottom-left-radius: 10px;
   right: 0;
   z-index: 1;
   top: 50px;
}

@media (max-width:600px){
    .home-container{
        margin-left: -13px;
        height: 250px;
    }
    .home-content {
        padding-top: 90px;
        text-align: center;
      }
      .home-content-toggle {
        margin-left: 0px;
      }
  
      .hire-me{
        width: 130px;
        margin: 0px 5px;
    }
    .hire-me:hover{
        width: 130px;
    }
    .btn-cv{
        background-color: tomato;
        width: 130px;
        color: white;
        margin: 0px 5px;
    }
    .home-content h1{
        font-size: 25px;
    }
  }    
