.mobile-nav{
    overflow: hidden;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1;
    
}
.mobile-nav-header{
    width: 100%;
    padding: 8px;
    background-color: #1e1e1e;
    /* height: 50px; */
}
.mobile-nav-title{
    text-align: center;
    color: tomato;
    font-weight: bold;
    margin-left: 20px;
    text-transform: capitalize;
    vertical-align: middle;
    font-size: 20px;
}
.mobile-nav-icon{
    color: white;
    cursor: pointer;
}
.nav-list{
    margin-top: -5px;
}
.mobile-nav-menu{
    padding-left: 8px;
    width: 180px;
    background-color: #1e1e1e;
    padding-top: 20px;
    padding-bottom: 10px;    
    border-bottom-right-radius: 10px;
}

@media (min-width:600px) {
    .mobile-nav{
        display: none;
    }
}