.tech-stack hr{
    width: 41%;
    margin:  0 auto;
    border:  2px solid grey;
    margin-bottom: 15px;
}
.media{ 
font-size: 25px;

}
.align-self-center{
    margin: 0px 5px;
}
.hover{
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.hover:hover{
    background-color: tomato;
    color: white;
    opacity: 80%;
    transition: 0.7s ease;
   cursor: pointer;
}
  .heading {
    color: tomato;
    text-transform: uppercase;
    display: inline-block;
    border-bottom: 3px solid tomato;
    margin-bottom: 30px;
}
@media (max-width:600px){
    .sidebar-width{
        margin: 0;
        padding: 0px 20px;
        margin-top: 20px;

    }
}